.Welcome {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100vh;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
}

form {
    width: 90%;
    margin: 0 auto;
    max-width: 400px;
}


button {
    margin: 1rem auto;
    display: inline-block;
    background: #f0e0a2;
    border: none;
    border-radius: 3px;
    padding: 1rem 1.5rem;
    font-size: 1rem;
    color: #333;
    font-family: 'Slabo 27px', serif;
    cursor: pointer;
    -webkit-transition: background 300ms;
    -o-transition: background 300ms;
    transition: background 300ms;
}

button:hover {
    background: #ebd88c;
}



